<!-- src/views/VideoView.vue -->
<template>
  <div class="video-container">
    <!-- 视频列表页面 -->
    <div v-if="!selectedVideo" class="video-list">
      <div 
        v-for="video in videos" 
        :key="video.id" 
        class="video-item"
        @click="selectVideo(video)"
      >
        <img :src="video.titleImageURL" :alt="video.title" class="video-thumbnail">
        <div class="video-title">{{ video.title }}</div>
      </div>
    </div>

    <!-- 视频详情页面 -->
    <div v-else class="video-detail">
      <button @click="selectedVideo = null" class="back-button">返回列表</button>
      <h2>{{ selectedVideo.title }}</h2>
      
      <!-- 视频简介 -->
      <div v-if="videoContent" class="video-description">
        {{ videoContent }}
      </div>

      <!-- 分P列表 -->
      <div class="video-parts">
        <div 
          v-for="part in videoParts" 
          :key="part.partName" 
          class="video-part"
          @click="selectPart(part)"
        >
          {{ part.partName }}
        </div>
      </div>

      <!-- 视频播放器 -->
      <div v-if="selectedPart" class="video-player">
        <video 
          controls 
          v-for="file in selectedPartFiles" 
          :key="file.id"
        >
          <source :src="file.url" type="video/mp4">
          您的浏览器不支持视频播放
        </video>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import axios from 'axios'

export default {
  name: 'VideoView',
  setup() {
    const videos = ref([])
    const selectedVideo = ref(null)
    const videoContent = ref('')
    const videoParts = ref([])
    const selectedPart = ref(null)
    const selectedPartFiles = ref([])
    const baseJSONUrl = ref('https://raw.githubusercontent.com/3542Q/JSONRepository/main');

    // 获取视频列表
    const fetchVideos = async () => {
      try {
        const response = await axios.get(`${baseJSONUrl.value}/videos/videos.json`)
        videos.value = response.data
      } catch (error) {
        console.error('获取视频列表失败', error)
      }
    }

    // 选择视频
    const selectVideo = async (video) => {
      selectedVideo.value = video

      // 获取视频简介
      try {
        const contentResponse = await axios.get(video.contentURL)
        videoContent.value = contentResponse.data

        // 获取视频分P列表
        const partsResponse = await axios.get(video.partsURL)
        videoParts.value = partsResponse.data
      } catch (error) {
        console.error('获取视频详情失败', error)
      }
    }

    // 选择分P
    const selectPart = async (part) => {
      selectedPart.value = part

      try {
        const filesResponse = await axios.get(part.partFilesURL)
        selectedPartFiles.value = filesResponse.data
      } catch (error) {
        console.error('获取分P文件失败', error)
      }
    }

    onMounted(fetchVideos)

    return {
      videos,
      selectedVideo,
      videoContent,
      videoParts,
      selectedPart,
      selectedPartFiles,
      selectVideo,
      selectPart
    }
  }
}
</script>

<style scoped>
.video-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.video-list {
  display: flex;
  flex-direction: column;
}

.video-item {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  cursor: pointer;
  border: 1px solid #ddd;
  padding: 10px;
}

.video-thumbnail {
  width: 100px;
  height: 100px;
  object-fit: cover;
  margin-right: 15px;
}

.video-detail {
  text-align: center;
}

.video-parts {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.video-part {
  margin: 0 10px;
  cursor: pointer;
  padding: 5px 10px;
  border: 1px solid #ddd;
}

.video-player video {
  max-width: 100%;
  margin: 10px 0;
}
</style>